@use "variables" as v;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: v.$amarillo;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: v.$fuente_principal;
  font-size: 1.6rem;
}
.contenedor {
  width: 95%;
  margin: 0 auto;
  position: relative;
}
a {
  text-decoration: none;
}

img {
  max-width: 100%;
}
