.caja-glitch {
  position: relative;
  height: 5rem;
  width: fit-content;
  line-height: 40px;
  display: flex;
  overflow: hidden;
  margin: 0px auto;
  z-index: -40;
}

.caja-glitch ul {
  list-style: none;
  // margin: 0px auto;
  animation: cambiar 18s infinite;
  padding: 5px 20px;
}

@keyframes cambiar {
  0% {
    margin-top: 0;
  }
  2% {
    margin-top: 0;
  }

  7% {
    margin-top: -40px;
  }
  9% {
    margin-top: -40px;
  }

  14% {
    margin-top: -80px;
  }
  16% {
    margin-top: -80px;
  }

  21% {
    margin-top: -120px;
  }
  23% {
    margin-top: -120px;
  }

  28% {
    margin-top: -160px;
  }
  30% {
    margin-top: -160px;
  }

  37% {
    margin-top: -200px;
  }
  39% {
    margin-top: -200px;
  }

  44% {
    margin-top: -240px;
  }
  46% {
    margin-top: -240px;
  }

  51% {
    margin-top: -280px;
  }
  52% {
    margin-top: -280px;
  }

  /* Return*/

  57% {
    margin-top: -240px;
  }
  59% {
    margin-top: -240px;
  }

  64% {
    margin-top: -200px;
  }
  66% {
    margin-top: -200px;
  }

  71% {
    margin-top: -160px;
  }
  72% {
    margin-top: -160px;
  }

  77% {
    margin-top: -120px;
  }
  79% {
    margin-top: -120px;
  }

  84% {
    margin-top: -80px;
  }
  86% {
    margin-top: -80px;
  }

  90% {
    margin-top: -40px;
  }
  92% {
    margin-top: -40px;
  }

  94% {
    margin-top: 0;
  }
  100% {
    margin-top: 0;
  }
}

@keyframes opacity {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@keyframes change {
  0%,
  12.66%,
  100% {
    transform: translate3d(0, 0, 0);
  }
  16.66%,
  29.32% {
    transform: translate3d(0, -25%, 0);
  }
  33.32%,
  45.98% {
    transform: translate3d(0, -50%, 0);
  }
  49.98%,
  62.64% {
    transform: translate3d(0, -75%, 0);
  }
  66.64%,
  79.3% {
    transform: translate3d(0, -50%, 0);
  }
  83.3%,
  95.96% {
    transform: translate3d(0, -25%, 0);
  }
}
